import React from 'react';
import MaterialTable from 'material-table';
import {connect} from "react-redux";
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableCell from "@material-ui/core/TableCell/TableCell";
import TableBody from "@material-ui/core/TableBody";
import {IconPack} from "../../../Images/IconPack";
import TextField from "@material-ui/core/TextField/TextField";
import InputLabel from "@material-ui/core/InputLabel/InputLabel";
import { setNewValueForStoreItem, createItem, deleteItem } from "../../../redux/licenses/corporate/generate/generate.actions";
import Button from "@material-ui/core/Button";
import { FormattedMessage } from 'react-intl';


const CorporateGenerate = (props) => {



    const addHandler = () => {
        const reducerStore =  props.generateData.store;
        const reducerDefData =  props.generateData.defValues;
        let ticketID = + new Date();


        let newObject = {
            login: reducerStore[reducerStore.length-1].login + 1,
            password: reducerDefData.password,
            nickname: reducerDefData.nickname,
            telegram: reducerDefData.telegram,
            comment: reducerDefData.comment,
            ticketID: ticketID,
        };

        props.createItem(newObject);
    }

    const getRowItem = (itemData) => {

        return (<TableRow className="inputs-row">
                <TableCell className="padding-sides-10 color-white">
                    <TextField
                        fullWidth
                        value={'CU' + itemData.login}
                        onChange={(e)=> {props.setNewValueForStoreItem('login', e.target.value, itemData.ticketID)}}
                        variant="filled"
                        disabled
                    />
                </TableCell>
                <TableCell className="padding-sides-10 color-white">
                    <TextField
                        fullWidth
                        value={itemData.password}
                        onChange={(e)=> {props.setNewValueForStoreItem('password', e.target.value, itemData.ticketID)}}
                        variant="filled"
                    />
                </TableCell>
                <TableCell className="padding-sides-10 color-white">
                    <TextField
                        fullWidth
                        value={itemData.nickname}
                        onChange={(e)=> {props.setNewValueForStoreItem('nickname', e.target.value, itemData.ticketID)}}
                        variant="filled"
                    />
                </TableCell>
                <TableCell className="padding-sides-10 color-grey">
                    <TextField
                        fullWidth
                        value={itemData.telegram}
                        onChange={(e)=> {props.setNewValueForStoreItem('telegram', e.target.value, itemData.ticketID)}}
                        variant="filled"
                    />
                </TableCell>
                <TableCell className="padding-sides-10 color-grey">
                    <TextField
                        fullWidth
                        value={itemData.comment}
                        onChange={(e)=> {props.setNewValueForStoreItem('comment', e.target.value, itemData.ticketID)}}
                        variant="filled"
                        className="w80p"
                    />
                    <div className="generateTable__btnsWrapper">
                        {props.generateData.store.length > 1 ? (<Button className="btn--table" onClick={()=>{props.deleteItem(itemData.ticketID)}}>{IconPack.buttons.minus}</Button>) : null }
                        <Button className="btn--table" onClick={()=>{addHandler()}}>{IconPack.buttons.plus}</Button>
                    </div>
                </TableCell>
            </TableRow>
        );}


    return (
        <div className="ÑustomStyle">
            <div className="alertPanel">
                <p className="color-white">Prepare accounts for their future owners. It is not necessary to write down the login and password at this stage, you can easily do it later</p>
                <span className="btn-text color-blue">MORE</span>
            </div>
            <Table className="configTable">
                <TableHead>
                    <TableRow>
                        <TableCell className="padding-sides-20 "><FormattedMessage id='table.login' /></TableCell>
                        <TableCell className="padding-sides-10 "><FormattedMessage id='table.password' /></TableCell>
                        <TableCell className="padding-sides-10"><FormattedMessage id='table.nickname' /></TableCell>
                        <TableCell className="padding-sides-10"><FormattedMessage id='table.telegram' /></TableCell>
                        <TableCell className="padding-sides-10 w500"><FormattedMessage id='table.commentRoleOrMore' /></TableCell>
                    </TableRow>
                </TableHead>
                <TableBody className="no-hover">
                    {props.generateData.store.map(itemData => {
                        return getRowItem(itemData);
                    })}

                </TableBody>
            </Table>
        </div>
    );
}

const mapStateToProps = (state) => ({
    generateData: state.corporateLicenseGenerate.generate,
});

const mapDispatchToProps = (dispatch) => {
    return {
        setNewValueForStoreItem: (item, itemValue, id) => dispatch(setNewValueForStoreItem(item, itemValue, id)),
        createItem: (object) => dispatch(createItem(object)),
        deleteItem: ( id ) => dispatch(deleteItem( id )),
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(CorporateGenerate);
